@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
* {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

body {
	font-family: 'Lato', 'Helvetica Neue', sans-serif;
	background-color: #f4f4f7;
}

h1 {
	/* font-family: 'Anton', sans-serif; */
	margin: 15px 0;
	text-align: center;
}

a.link-unstyled {
	color: inherit;
}

a.link-unstyled:hover {
	color: inherit;
	text-decoration: none;
}

.horizontal-separator {
	margin: 40px 0;
	border-bottom: #000 solid 1px;
}

.btn {
	padding: 0.375rem 2rem;
}

.btn.btn-primary {
	color: #fff;
	background-color: #7dcd40;
	border-color: #7dcd40;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
	color: #fff;
	background-color: #7dcd40;
	border-color: #7dcd40;
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
	color: #fff;
	background-color: #7dcd40;
	border-color: #7dcd40;
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
	box-shadow: 0 0 0 0.2rem rgba(125, 205, 64, 0.5);
}

.btn.btn-danger {
	color: #fff;
	background-color: #cd4040;
	border-color: #cd4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
	color: #fff;
	background-color: #cd4040;
	border-color: #cd4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
	color: #fff;
	background-color: #cd4040;
	border-color: #cd4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
	box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
}



* {
	padding: 0;
	margin: 0;
	text-decoration: none;
	list-style: none;
	box-sizing: border-box;
}

nav {
	background-color: #222;
	display: flex;
	height: 80px;
	justify-content: space-between;
	width: 100%;
}

nav a.nav__logo {
	color: white;
	font-size: 25px;
	line-height: 80px;
	padding: 0 15px;
}

nav ul {
	margin-right: 20px;
}

nav ul li {
	display: inline-block;
	line-height: 80px;
	margin: 0 5px;
}

nav ul li a {
	border-radius: 3px;
	color: #fff;
	font-size: 15px;
	padding: 10px 13px;
	text-transform: uppercase;
}

a:hover {
	border-bottom: #F58A07 solid 3px;
	color: #fff;
	text-decoration: none;
}

.checkbtn {
	font-size: 30px;
	color: white;
	/* float: right; */
	line-height: 80px;
	margin-right: 40px;
	cursor: pointer;
	display: none;
}

#check {
	display: none;
}

@media (max-width: 540px) {
	.checkbtn {
		display: block;
	}

	nav a.nav__logo {
		padding: 0;
	}


	nav ul {
		position: fixed;
		width: 100%;
		height: 100vh;
		background: #222;
		top: 80px;
		left: -100%;
		text-align: center;
		transition: all 0.5s;
		z-index: 1000;
	}

	nav ul li {
		display: block;
		margin: 50px 0;
		line-height: 30px;
	}

	a:hover {
		font-weight: bold;
	}

	#check:checked ~ ul {
		left: 0;
	}
}

footer {
    
}

footer a {
    color: black;
    text-decoration: underline;
}

footer a:hover {
    color: black;
    cursor: pointer;
}
.PageLoading {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    width: 100%;
  }
.lds-grid {
	display: inline-block;
	position: relative;
	width: 64px;
	height: 64px;
}

.lds-grid div {
	position: absolute;
	width: 13px;
	height: 13px;
	border-radius: 50%;
	background: #000;
	-webkit-animation: lds-grid 1.2s linear infinite;
	        animation: lds-grid 1.2s linear infinite;
}

.lds-grid div:nth-child(1) {
	top: 6px;
	left: 6px;
	-webkit-animation-delay: 0s;
	        animation-delay: 0s;
}

.lds-grid div:nth-child(2) {
	top: 6px;
	left: 26px;
	-webkit-animation-delay: -0.4s;
	        animation-delay: -0.4s;
}

.lds-grid div:nth-child(3) {
	top: 6px;
	left: 45px;
	-webkit-animation-delay: -0.8s;
	        animation-delay: -0.8s;
}

.lds-grid div:nth-child(4) {
	top: 26px;
	left: 6px;
	-webkit-animation-delay: -0.4s;
	        animation-delay: -0.4s;
}

.lds-grid div:nth-child(5) {
	top: 26px;
	left: 26px;
	-webkit-animation-delay: -0.8s;
	        animation-delay: -0.8s;
}

.lds-grid div:nth-child(6) {
	top: 26px;
	left: 45px;
	-webkit-animation-delay: -1.2s;
	        animation-delay: -1.2s;
}

.lds-grid div:nth-child(7) {
	top: 45px;
	left: 6px;
	-webkit-animation-delay: -0.8s;
	        animation-delay: -0.8s;
}

.lds-grid div:nth-child(8) {
	top: 45px;
	left: 26px;
	-webkit-animation-delay: -1.2s;
	        animation-delay: -1.2s;
}

.lds-grid div:nth-child(9) {
	top: 45px;
	left: 45px;
	-webkit-animation-delay: -1.6s;
	        animation-delay: -1.6s;
}

@-webkit-keyframes lds-grid {
	0%,
	100% {
		opacity: 1;
	}

	50% {
		opacity: 0.5;
	}
}

@keyframes lds-grid {
	0%,
	100% {
		opacity: 1;
	}

	50% {
		opacity: 0.5;
	}
}

.PageError {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
	width: 100%;
}

.team-card {
	background-color: #f5f5f5;
	border-radius: 10px;
	box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);

	align-items: center;

	margin: 20px 0;
	padding: 20px 15px;
}

.team-card__badge {
	display: flex;
	height: inherit;
	justify-content: center;
}

.team-card__data {
	height: inherit;
}

.team-card__data__team-name {
    font-size: 1.5rem;
    font-weight: bold;
	margin-right: 5px;
}
.team-card__data__team-owner {
	font-size: 1rem;
}
.team-card__data__team-description {
	font-size: 1.2rem;
}

thead tr td {
    font-weight: bold;
}

td {
    text-align: center;
}
